#proLogo {
  width: 60px;
  height: 60px;
  cursor: pointer;
  padding-bottom: 20px;
  padding-right: 20px;
}
#presentation {
  margin-bottom: 100px;
}
#proBody {
  position: relative;
  width: 100%;
  padding: 40px;
  box-shadow: 5px 5px 5px gainsboro, -1em 0 0.4em gainsboro;
  background-color: rgb(255, 221, 227);
}
#alignLogoBrand {
  display: flex;
  flex-direction: row;
}

#titreCentre {
  text-align: center;
}
.open {
  cursor: pointer;
}
.close {
  position: absolute;
  color: red;
  font-size: xx-large;
  top: 0;
  right: 0;
  padding: 20px;
  cursor: pointer;
}

.infoClose {
  font-style: italic;
  text-align: center;
}
.titrePro2 {
  color: gray;

  padding-top: 20px;
}
.titrePro3 {
  margin-top: 10px;
  color: black;
}
.titrePro4 {
  color: rgb(250, 93, 216);
  padding-top: 20px;
}

